	module.exports = {"data":{"wordpressPost":{"title":"The BECCA Chrissy Teigen Palette Is Here And Now We Can All Look Like Her","content":"<p>She&#8217;s a model, mum, cook book author, culinary wiz and, much to our endless delight, the owner of a razor sharp wit and glorious sense of humour. Yep &#8211; we &#8216;re talking about the ultimate <strong>California cool girl Chrissy Teigen</strong>. Now, Chrissy can add another string to her bow with the release of a limited edition glow face palette.</p>\n<p>The palette,<strong> a BECCA and Chrissy T collab</strong>, is designed to provide you with summer skin all year round &#8211; and it does exactly what it promises. So a little detail &#8230;</p>\n<p>The palette features BECCA&#8217;s trademark creamy blend formula. This means that it goes on easy and stays put yet never EVER gets powdery or dry. It contains two highlighters <strong>(Beach Nectar which is kind of a juicy apricot shade and Rose Gold &#8211; a subtle gold with a hint of pink)</strong>, a blush and a bronzer. You can wear each separately or combine all four for a warm, sun-kissed, sultry look.</p>\n<p>Chrissy was involved in every step of the creation of this palette including the choice of shades. She loves them all, obviously, but has declared <strong>Rose Gold her favourite.</strong> Us? We&#8217;re actually pretty smitten with the blush &#8211; it&#8217;s the perfect pink and gives you that &#8216;I just strolled along the beach at sunset&#8217; kind of effect.</p>\n<p>And hey &#8211; if it gives us even half the perfectly pretty glow that Chrissy is so well known for then we consider it a must-buy!</p>\n<img class=\"size-medium wp-image-2287 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203202/BECCAXCHRISSY-PALETTE-IMAGE2-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203202/BECCAXCHRISSY-PALETTE-IMAGE2-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203202/BECCAXCHRISSY-PALETTE-IMAGE2-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203202/BECCAXCHRISSY-PALETTE-IMAGE2-768x768.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203202/BECCAXCHRISSY-PALETTE-IMAGE2-1024x1024.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><a href=\"https://www.sephora.com.au/products/becca-becca-x-chrissy-teigen-glow-face-palette?q=Becca%20Chrissy%20T\"><strong>The BECCA X Chrissy Teigen Glow Face Palette, $78, is exclusive to Sephora</strong></a></p>\n<img class=\"size-medium wp-image-2288 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203257/JPG-BECCA_CT_Swatches_Group-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203257/JPG-BECCA_CT_Swatches_Group-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203257/JPG-BECCA_CT_Swatches_Group-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203257/JPG-BECCA_CT_Swatches_Group-768x768.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203257/JPG-BECCA_CT_Swatches_Group-1024x1024.jpg 1024w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01203257/JPG-BECCA_CT_Swatches_Group.jpg 1600w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/01201712/BECCA-Chrissy.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"4d5363a7-8eae-512f-aa1b-c114e41d64ce"}}

